<template>
	<view class="like">
		<view class="page_title" v-if="likes.length === 0">
			暂无收藏
			<button class="go-home" @click="handleHome"> 回到首页 </button>
		</view>
		<view class="page_title" v-else>
			我的收藏 {{pagination.total}}
		</view>
		<view class="items">
			<view class="item" v-for="item in likes" :key="item.id" @click="go_article(item.article.id)">
				<view class="info">
					<view class="title">
						{{item.article.title}}
					</view>
					<view class="body">
						<view class="left">
							<view class="name">{{item.article.subTitle}}</view>
							<view class="line" />
							<view class="updata">{{item.article.createdAt | dayFormat }}</view>
						</view>
						<view class="right">
							<image src="/static/images/comments.png" mode="" />
							<view class="comment">{{item.article.commentsCount}}</view>
						</view>
					</view>
				</view>
				<image class="thumb" :src="item.article.image" mode=""></image>
			</view>
		</view>
	</view>
</template>
<script>
	export default{
		data(){
			return{}
		},
		onLoad() {
			this.init()
		},
		methods:{
			
		}
	}
</script>

